define('js/components/milestones',["react",
        "js/utils",
        "js/components/mixins/loader",
        "js/components/mixins/github_error_handler",
        "jquery",
        "moment"
        ],
        function (React,Utils,LoaderMixin,GithubErrorHandlerMixin,$,Moment) {
        'use'+' strict';


        var MilestoneItem = React.createClass({displayName: "MilestoneItem",

            render : function(){
                var due;
                if (this.props.milestone.due_on !== null){
                    var datestring = Moment(new Date(this.props.milestone.due_on)).fromNow();
                    due = [React.createElement("i", {className: "octicon octicon-clock"}),' ',datestring];
                }
                return React.createElement("div", {className: "col-md-3"}, 
                    React.createElement("div", {className: "panel panel-primary milestone-item"}, 
                        React.createElement(A, {href: Utils.makeUrl("/sprintboard/"+this.props.repository.full_name+"/"+this.props.milestone.number)}, 
                            React.createElement("div", {className: "panel-body"}, 
                                React.createElement("h4", null, this.props.milestone.title), 
                                React.createElement("span", {className: "label label-danger"}, this.props.milestone.open_issues, " open"), "", 
                                React.createElement("span", {className: "label label-success"}, this.props.milestone.closed_issues, " closed"), 
                                React.createElement("span", {className: "pull-right"}, due)
                            )
                        )
                    )
                );
            }
        });

        var Milestones = React.createClass({

            mixins : [LoaderMixin,GithubErrorHandlerMixin],

            resources : function(props){
                return [
                        {
                            name : 'repository',
                            endpoint : this.apis.repository.getDetails,
                            params : [props.data.repositoryId,{}],
                            success : function(data){
                                return {repository : data};
                            }.bind(this)
                        },
                        {
                        name : 'milestones',
                        endpoint : this.apis.milestone.getMilestones,
                        params : [props.data.repositoryId,{per_page : 100}],
                        success : function(data,xhr){

                            var arr = [];
                            for(var i in data) {
                                if(data.hasOwnProperty(i) && !isNaN(+i)) {
                                    arr[+i] = data[i];
                                }
                            }
                            return {milestones : arr};
                        }.bind(this)
                       }];
            },

            displayName: 'Milestones',

            render: function () {

                var data = this.state.data;

                var milestoneItems = data.milestones.map(function(milestone){
                    return React.createElement(MilestoneItem, {milestone: milestone, repository: data.repository});
                }.bind(this))

                if (milestoneItems.length == 0)
                    milestoneItems = [React.createElement("p", {className: "alert alert-info"}, "Seems there is nothing to show here.")]

                return React.createElement("div", {className: "container"}, 
                    React.createElement("div", {className: "row"}, 
                        React.createElement("div", {className: "col-md-12"}, 
                            React.createElement("h3", null, "Milestones - ", data.repository.name)
                        )
                    ), 
                    React.createElement("div", {className: "row"}, 
                        milestoneItems
                    ), 
                    React.createElement("div", {className: "row"}, 
                        React.createElement("div", {className: "col-md-12"}, 
                            React.createElement(A, {href: Utils.makeUrl('/sprintboard/'+data.repository.full_name)}, "show issues without a milestone")
                        )
                    )
                );
            }
        });

        return Milestones;
});

