/**
 * @jsx React.DOM
 */

define('js/components/mixins/github_error_handler',[
  "react",
  "js/utils",
  "jquery",
  "js/components/generic/modal"
],function (
  React,
  Utils,
  $,
  Modal
) {
    'use strict';

    var GithubErrorHandler = {

        renderErrorPage : function(errorData){
            var errorMessages = [];
            for(var resource in errorData){
                var data = errorData[resource];
                var remainingRequests = data.getResponseHeader('X-RateLimit-Remaining');
                var githubMessage = data.responseJSON.message;
                if (remainingRequests == 0){
                    var message;
                    if (Utils.isLoggedIn())
                        message = React.createElement("p", null, "It seems that the rate limit is exceeded. Please wait a while before trying again.");
                    else
                        message = React.createElement("p", null, "It seems that you have exceeded the rate limit of the Github API. You can increase that limit by logging in.", React.createElement("br", null), React.createElement(A, {className: "btn btn-md btn-success", href: Utils.makeUrl("/login")}, "log in to increase rate limit"));

                    errorMessages.push(React.createElement("div", null, 
                        message
                    ));
                }
                else
                    errorMessages.push(React.createElement("div", null, React.createElement("p", {className: "alert alert-danger"}, "The following error occurred when querying the Github API: ", githubMessage)));
                break;
            }
           return React.createElement("div", {className: "container"}, 
                React.createElement("div", {className: "row"}, 
                    React.createElement("div", {className: "col-md-4 col-md-offset-4"}, 
                        React.createElement("div", {className: "well bs-component"}, 
                            React.createElement("h3", null, "Oh no! An error occurred..."), 
                            errorMessages
                        )
                   )
                )
            );

        }
    };

    return GithubErrorHandler;
});

